.navbar {
  font-family: $navbar-font-family;
  @include border-radius(0);
  box-shadow: none;
  //    @include box-shadow(0px 0px 20px 0px rgba(0,0,0,0.15));
  border: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  padding: 0;

  .navbar-brand {
    @include three-dots-overflow();
  }

  .navbar-custom-right-menu {
    float: right;
  }

  .navbar-toggle {
    text-decoration: none;
    color: #fff;
    width: 20px;
    height: 20px;
    margin-top: -4px;
    margin-right: 17px;

    &:before {
      content: '\E8D5';
      font-family: 'Material Icons';
      font-size: 26px;
    }
  }

  .navbar-collapse {
    &.in {
      overflow: visible;
    }
  }

  .nav.navbar-nav {
    display: block;
    line-height: 55px;
  }

  .container-fluid,
  .container {
    display: block;
  }

  .dropdown-menu ul.menu li {
    width: 100%;
  }

  .nav-left-menu {
    margin: 3px 15px;
  }
  .collapse-menu-icon {
    margin-bottom: 0px;
    line-height: 55px;
    padding-left: 35px;
  }

  .header-icon {
    vertical-align: top !important;
    .feather {
      height: 20px;
      width: 20px;
    }
  }
}

.ls-closed {
  .sidebar {
    margin-left: -300px;
  }

  section.content {
    margin-left: 15px;
  }

  .bars {
    &:after,
    &:before {
      font-family: 'Material Icons';
      font-size: 24px;
      position: absolute;
      left: 10px;
      top: 3px;
      margin-right: 10px;
      @include transform(scale(0));
      @include transition(all 0.3s);
    }

    &:before {
      content: '\E5D2';
      @include transform(scale(1));
    }

    &:after {
      content: '\E5C4';
      @include transform(scale(0));
    }
  }

  .navbar-brand {
    margin-left: 30px;
  }
}

.overlay-open {
  .bars {
    &:before {
      @include transform(scale(0));
    }

    &:after {
      @include transform(scale(0));
      /*@include transform(scale(1));*/
    }
  }

  &.ls-closed {
    .sidebar {
      margin-left: 0;
    }
  }
}

.navbar-header {
  padding: 8px 7px;
  width: 265px;
  @include transition(all 0.5s);

  .bars {
    float: left;
    text-decoration: none;
  }
}

.logo-name {
  color: white;
  font-size: 24px;
  font-weight: 500;
}

.navbar-nav {
  > li {
    > a {
      padding: 7px 7px 2px 7px;
      margin-top: 12px;

      &.js-right-sidebar {
        margin-right: 20px;
      }
    }
  }

  &.navbar-right {
    float: right !important;
    margin-right: 7px;
    line-height: 60px;
    .nav-item {
      margin: 0px 4px;
    }
  }

  .dropdown-menu {
    margin-top: 50px !important;
    width: 325px;
    right: 0;
    left: auto;
    position: absolute !important;
    top: 0;
    padding: 0;
    border-radius: 5px;

    // &::before {
    //   content: '';
    //   position: absolute;
    //   top: -7px;
    //   right: 19px;
    //   display: inline-block !important;
    //   border-right: 7px solid transparent;
    //   border-bottom: 7px solid #eee;
    //   border-left: 7px solid transparent;
    //   border-bottom-color: rgba(0, 0, 0, 0.2);
    // }

    // &::after {
    //   content: '';
    //   position: absolute;
    //   top: -6px;
    //   right: 20px;
    //   display: inline-block !important;
    //   border-right: 6px solid transparent;
    //   border-bottom: 6px solid #fff;
    //   border-left: 6px solid transparent;
    // }

    ul.menu .menu-info p {
      line-height: 1;

      .material-icons {
        display: inline-block;
      }
    }

    li.footer {
      width: 100%;
      height: 45px;
    }

    .header {
      line-height: 2;
    }
  }

  .material-icons {
    line-height: 1;
    height: 24px;
  }

  .fas {
    line-height: 1;
    height: 24px;
    font-size: 18px;
  }

  .far {
    line-height: 1;
    height: 24px;
    font-size: 18px;
  }

  .fab {
    line-height: 1;
    height: 24px;
    font-size: 18px;
  }

  .fa {
    line-height: 1;
    height: 24px;
    font-size: 18px;
  }

  .user_profile {
    .user_dw_menu {
      list-style-type: none;
      padding-left: 0px;
      li {
        width: 100%;
        border-bottom: 1px solid #eee;
        height: 45px;
        a {
          line-height: 24px;
          color: #333333;
          padding: 8px 15px;
          display: inline-flex;
        }

        i {
          float: left;
          padding-right: 5px;
        }

        &:last-child {
          border-bottom: 0;
        }
      }
    }

    .dropdown-menu {
      width: 200px;
      right: -10px;
    }
  }
}
.nav-notification-icons {
  &:after {
    display: none;
  }
}
.lang-dropdown {
  padding: 5px 5px !important;
  cursor: pointer;
  width: 35px;
  .country-name {
    vertical-align: middle;
  }
  &::after {
    display: none;
  }
}
.lang-item {
  width: 175px !important;
  .lang-item-list {
    line-height: 30px;
    &.active {
      background-color: #eef1f9;
      color: #000000;
    }
    &:active {
      background-color: #eef1f9;
      color: #000000;
    }
    .flag-img {
      margin: 0px 5px;
    }
  }
}
.notification-dropdown {
  li a {
    border-bottom: 0px !important;
  }
  .nfc-header {
    padding: 20px;
    background-color: #7366ff;
    border-radius: 5px 5px 0px 0px;
    h5 {
      color: #fff;
    }
  }
}
.label-count {
  position: absolute;
  top: 5px;
  right: 10px;
  font-size: 10px;
  line-height: 15px;
  background-color: #000;
  padding: 3px 3px;
  border-radius: 3px;
}

.navbar-brand {
  float: left;
  height: 45px;
  padding: 6px 25px;
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  width: 100%;

  img {
    /*float: left;*/
    vertical-align: top;
  }

  span {
    line-height: 32px;
    padding-left: 10px;
  }
}

.navbar-collapse {
  padding-right: 15px;
  padding-left: 15px;
  overflow-x: visible;
  border-top: 1px solid transparent;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.collapse {
  display: none;
}

.navbar-nav {
  margin: 0px -15px;

  > li > a {
    padding-top: 10px;
    padding-bottom: 10px;
    line-height: 20px;
  }
}

.nav {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;

  > li {
    position: relative;
    display: block;

    > a {
      position: relative;
      display: block;
      padding: 8px 4px;
    }
  }
  .fullscreen {
    margin: 0px 10px;
  }
  .langSelItem {
    margin: 0px 10px;
  }
  .btnNotification {
    margin: 0px 10px;
  }
  .btnUserDropdown {
    margin: 0px 10px;
  }
  .btnHeaderSetting {
    margin: 0px 10px;
    .settingBtn {
      vertical-align: top;
    }
  }
  .user-menu-icons {
    margin: 0px 5px;
    .feather {
      height: 18px !important;
      width: 18px !important;
      vertical-align: middle;
    }
  }
}

nav.navbar {
  min-height: 60px;
}

@each $key, $val in $colors {
  .col-#{$key} {
    .navbar {
      @include navbar-link-color(#fff, #000, 0.95);
      // @include navbar-link-color(rgba(0,0,0,0.85), #000, .95);
    }
  }
}

.side-closed {
  .sidebar {
    /*margin-left: -300px;*/
    width: 60px;

    .menu {
      .list {
        li {
          .menu-toggle {
            &:before,
            &:after {
              content: '';
            }
          }

          span {
            display: none;
          }

          a {
            line-height: 1rem;
            padding: 9px 9px 3px 8px;

            &:before,
            &:after {
              top: calc(50% - 7px);
            }
          }
        }
      }
    }
  }

  &.submenu-closed {
    .sidebar .menu .list {
      li .ml-menu,
      .header {
        display: none !important;
      }
    }

    .navbar-header {
      width: 65px;

      .navbar-brand {
        padding-right: 0;
        padding-left: 0;
        margin: 0;

        span {
          display: none;
        }
      }
    }

    .sidebar-user-panel {
      display: none;
    }
  }

  section.content {
    margin-left: 65px;
  }

  .navbar-brand {
    margin-left: 30px;
  }

  &.side-closed-hover {
    .sidebar {
      width: 260px;

      .menu {
        .list {
          li {
            .menu-toggle {
              &:before {
                content: '+';
              }

              &:after {
                content: '\2013';
              }
            }

            span {
              display: block;
              float: left;
            }

            a {
              padding: 9px 9px 9px 18px;
            }

            i {
              float: left;
              line-height: 2rem;
            }
            .sidebarIcon {
              float: left;
              line-height: 2rem;
            }
          }

          .ml-menu {
            li {
              a {
                padding-left: 50px;
                padding-top: 7px;
                padding-bottom: 7px;
                line-height: 2rem;
              }
            }
            .ml-menu-2 {
              li {
                a {
                  padding-left: 65px;
                  padding-top: 4px;
                  padding-bottom: 4px;
                }
              }
            }
            .ml-menu-3 {
              li {
                a {
                  padding-left: 85px;
                  padding-top: 4px;
                  padding-bottom: 4px;
                }
              }
            }
          }
        }
      }
    }

    section.content {
      margin-left: 276px;
    }
  }
}

.sidemenu-collapse {
  height: 50px;
  padding: 0px 20px 0px 44px;

  &:hover {
    text-decoration: none;
    // color: #fff;
    background-color: transparent;
  }

  .fas {
    line-height: 1;
    height: 24px;
    font-size: 18px;
    color: #3a2c70;
  }
}

nav,
nav .nav-wrapper i,
nav a.button-collapse,
nav a.button-collapse i {
  @media screen and (min-width: 601px) {
    height: 60px;
    line-height: 60px;
  }
}

.boxed-layout {
  .container > .navbar-header {
    margin-left: 0;
  }
}
